import React from 'react';

class CategoriaMarcaForm extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            categorias: [],
            marcas: []
        };
    }

    componentDidMount(){
        const administrador = JSON.parse(localStorage.getItem('administrador'));
        try {
            var myHeaders = new Headers();
            myHeaders.append("token", administrador.token);

            var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow'
            };

            fetch("http://66.232.110.36/api/categoria", requestOptions)
            .then(response => response.text())
            .then(result => {
                const categoriasRes = JSON.parse(result).categorias;
                this.setState({
                    categorias:categoriasRes
                })
            })
            .catch(error => console.log('error', error));

            fetch("http://66.232.110.36/api/marca", requestOptions)
            .then(response => response.text())
            .then(result => {
                const marcasRes = JSON.parse(result).marcas;
                this.setState({
                    marcas:marcasRes
                })
            })
            .catch(error => console.log('error', error));
        } catch (error) {
            
        }
    }
  render() {
    return (
      <div className="container bg-light border rounded p-4"> 
        <form onSubmit={this.props.onSubmit}>
          <div className="form-group">
            <label>Descripción: (obligatorio)</label>
            <input
              onChange={this.props.onChange}
              className="form-control"
              type="text"
              name="descripcion"
              value={this.props.formValues.descripcion}
            />
          </div>
          <button type="submit" className="btn btn-outline-success btn-block">
            Guardar
          </button>
        </form>
      </div>
    );
  }
}

export default CategoriaMarcaForm;